import { AppBar, Container, Avatar, Button, Tooltip, MenuItem, createTheme, IconButton, Typography, Menu, Toolbar, Box } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import AdbIcon from '@mui/icons-material/Adb';
import { ThemeProvider } from '@emotion/react';
import { useState, useEffect } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Cart from '../Cart/Cart.jsx';
import { validateLogin } from '../Cart/cart';
import { setCartInfo } from '../../redux/cart/cartSlice';
import { request } from '../../utils/request';
import Cookies from 'js-cookie';

const  defaultTheme = createTheme();

const logOut = async () => {
  const logOutResponse = request({ path: 'api/session/logout', method: 'GET' });
  const errorMessage = {
    '500': 'Error de conexión',
    '400': 'Parametros invalidos',
    '401': 'Usuario o contraseña incorrectos',
    'default': 'Error inesperado en el servidor'
  };
  const message = logOutResponse.status && logOutResponse.status !== 200 ? (errorMessage[logOutResponse.status] ?? errorMessage.default) : '';
  if (message.length) throw new Error(message);
  return logOutResponse
}

function ResponsiveAppBar() {
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const [state, setState] = useState({ loggedUser: false});
  const cartState = useSelector(state => state.cart);
  const { userRole } = cartState;
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const logOutUser = async () => {
    try {
      await logOut();
      Cookies.remove('jwt');
      dispatch(setCartInfo({ uid: null , cid: null, products: [], userRole: null } ));
      return navigate('/login');
    } catch (error) {
      console.error(error.message);
    }
  }

  const pages = [ { displayName: 'Productos', href: '/products' }, {displayName: 'Carro', href: '/cart' } ];
  const settings = [{ displayName: 'Logout', onClick: logOutUser }];

  const handleOpenNavMenu = (event) => setAnchorElNav(event.currentTarget);
  const handleOpenUserMenu = (event) => setAnchorElUser(event.currentTarget);
  const handleCloseNavMenu = () => setAnchorElNav(null);
  const handleCloseUserMenu = () => setAnchorElUser(null);

  useEffect(() => {
    let isSubscribed = true;
    try {
      const fetchData = async () => {
        const { logged, cid, uid, products, userRole } = await validateLogin(cartState);
        if (!logged) return (<Navigate to='/login'/>);
        if (isSubscribed && (uid !== cartState.userId || cid !== cartState.cartId)) {
          dispatch(setCartInfo({ cid, uid, products, userRole }));
        }
        if (isSubscribed) {
          setState(state => ({ ...state, loggedUser: true }));
        }
      }
      fetchData();
    } catch (error) {
      console.log(error.message);
      return (<Navigate to='/login'/>)
    }
    return () => isSubscribed = false;
  }, [dispatch]);

  return (
    <ThemeProvider theme={defaultTheme}>
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            EGY
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map(({displayName , href}) => (
                (userRole === 'Admin' && displayName === 'Carro') ? null :
                  <MenuItem key={displayName} onClick={() => navigate(href)}>
                  <Typography textAlign="center">{displayName}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            EGY
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            {pages.map(({ displayName , href }) => (
              (userRole === 'Admin' && displayName === 'Carro') ? null :
              <Button
                key={displayName}
                onClick={() => navigate(href)}
                sx={{ my: 2, color: 'white', display: 'block' }}
              >
                {displayName}
              </Button>
            ))}
          </Box>
          {userRole !== 'Admin' ? <Cart/> : null}
          <Box sx={{ my: 2, color: 'white', display: 'block' }}>
            <Tooltip title="Usuario">
              <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                <Avatar alt={'User'}/>
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {settings.map(({ displayName, onClick}) => (
                <MenuItem key={displayName} onClick={onClick}>
                  <Typography textAlign="center">{displayName}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
    </ThemeProvider>
  );
}
export default ResponsiveAppBar;